import React, { useState } from "react";

const AstromonBaseCard = ({ astromonData, onSelect }) => {
  const [searchQuery, setSearchQuery] = useState("");
  const [filteredStages, setFilteredStages] = useState([]);

  const handleSearch = (e) => {
    const query = e.target.value.toLowerCase();
    setSearchQuery(query);

    // Filter stages based on the search query
    const results = astromonData
      .filter((stages) => stages[0].toLowerCase().includes(query))
      .slice(0, 5); // Limit to first 5 results
    setFilteredStages(results);
  };

  const handleSelect = (stages) => {
    const selectedSlug = stages[0]; // Assuming the slug is the first stage
    onSelect(selectedSlug);
    setSearchQuery(""); // Clear search on selection
    setFilteredStages([]); // Clear results
  };

  return (
    <div className="astromon-base-card">
      <input
        type="text"
        value={searchQuery}
        onChange={handleSearch}
        placeholder="Search for an Astromon"
        onKeyPress={(e) => {
          if (e.key === "Enter" && filteredStages.length > 0) {
            handleSelect(filteredStages[0]); // Select first result on Enter
          }
        }}
      />

      <div className="search-results">
        {filteredStages.map((stages, index) => (
          <div
            key={index}
            className="result-item"
            onClick={() => handleSelect(stages)}
            style={{
              cursor: "pointer",
              padding: "5px",
              border: "1px solid black",
            }}
          >
            {stages.join("  ")} {/* Display stages in the desired format */}
          </div>
        ))}
      </div>
    </div>
  );
};

export default AstromonBaseCard;
